import React, { PureComponent } from 'react';
import classNames from 'classnames';
import '../style/index';
import './index.css';
export default class List extends PureComponent {
    render() {
        const { prefixCls, className, style } = this.props;
        const listClassNames = classNames(prefixCls, className);
        return (<div className={listClassNames} style={style}>
            </div>);
    }
}
List.defaultProps = {
    prefixCls: 'zzc-list',
    className: '',
    style: {}
};



